import Vue from 'vue';
import swal from 'vue-sweetalert2';
import VueRouter from 'vue-router';

Vue.use(VueRouter);
Vue.use(swal);
const routes = [
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Authentication/Login.vue'),
    meta: {
      forVisitor: true,
    },
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('../views/Authentication/Register.vue'),
    meta: {
      forVisitor: true,
    },
  },
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Dashboard.vue'),
    meta: {
      forVisitor: true,
    },
  },
  {
    path: '/dashboardkoperasi',
    name: 'dashboardkoperasi',
    component: () => import('../components/AuthenticatedUser/Koperasi/KSU/App.vue'),
    meta: {
      forKoperasi: true,
    },
    children: [
      {
        path: '',
        component: () => import('../components/AuthenticatedUser/Koperasi/Layout/Dashboard.vue'),
      },
      {
        path: '/anggotakoperasi',
        component: () => import('../components/AuthenticatedUser/Koperasi/Layout/AnggotaKoperasi.vue'),
        meta: {
          roolTo: true,
        },
      },
      {
        path: '/pengaturanpendaftarananggota',
        component: () => import('../components/AuthenticatedUser/Koperasi/Layout/PengaturanFieldDaftarKoperasi.vue'),
        meta: {
          roolTo: true,
        },
      },
      {
        path: '/daftaranggota',
        component: () => import('../components/AuthenticatedUser/Koperasi/Layout/DaftarAnggotaKoperasi.vue'),
        meta: {
          roolTo: true,
        },
      },
      {
        path: '/pengaturanpinjaman',
        component: () => import('../components/AuthenticatedUser/Koperasi/KSU/PengaturanPinjaman.vue'),
        meta: {
          roolTo: true,
        },
      }
    ],
  },
  {
    path: '/daftarkoperasi',
    name: 'daftarkoperasi',
    component: () => import('../components/AuthenticatedUser/Koperasi/Layout/DaftarKoperasi/DaftarKoperasi.vue'),
    meta: {
      forKoperasi: true,
    },
    children: [
      {
        path: '',
        component: () => import('../components/AuthenticatedUser/Koperasi/Layout/DaftarKoperasi/Layout/FormDaftar.vue'),
      },
      {
        path: '/pendingactivation',
        component: () => import('../components/AuthenticatedUser/Koperasi/Layout/DaftarKoperasi/Layout/PendingActivation.vue'),
      },
    ],
  },
  {
    path: '/dashboardanggotakoperasi',
    name: 'AnggotaKoperasi',
    component: () => import('../components/AuthenticatedUser/Anggota/App.vue'),
    meta: {
      forAnggota: true,
    },
    children: []
  },
  {
    path: '*',
    name: 'notfound',
    component: () => import('../views/NotFound.vue'),
  },
  {
    path: '/dashboardadmin',
    name: 'dashboaradmin',
    component: () => import('../components/AuthenticatedUser/Diskoperindag/App.vue'),
    meta: {
      forAdmin: true,
    },
    children: [
      {
        path: '',
        component: () => import('../components/AuthenticatedUser/Diskoperindag/Layout/AccKoperasiPending.vue'),
      },
      {
        path: '/semuakoperasi',
        component: () => import('../components/AuthenticatedUser/Diskoperindag/Layout/DaftarKoperasi.vue'),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
  scrollBehavior(to, from, next) {
    if (to.hash) {
      return { selector: to.hash };
    }
    return {
      x: 0,
      y: 0,
    };
  },
});
router.beforeEach(
  (to, from, next) => {
    if (to.matched.some(record => record.meta.forVisitor)) {
      if (Vue.auth.isAuthenticated()) {
        if (Vue.auth.getUserRole() === 'ROLE_koperasi') {
          if (parseInt(Vue.auth.isHaveKoperasi()) === 0) {
            next({
              path: '/daftarkoperasi',
            });
          } else if (parseInt(Vue.auth.isHaveKoperasi()) === 2) {
            next({
              path: '/pendingactivation',
            });
          } else if (parseInt(Vue.auth.isHaveKoperasi()) === 3) {
            next({
              path: '/dashboardkoperasi',
            });
          } else {
            next();
          }
        } else if (Vue.auth.getUserRole() === 'ROLE_admin') {
          next({
            path: '/dashboardadmin',
          });
        } else if (Vue.auth.getUserRole() === 'ROLE_anggota') {
          next({
            path: '/dashboardanggotakoperasi',
          });
        }
      } else {
        next();
      }
    } else if (to.matched.some(record => record.meta.forKoperasi)) {
      if (!Vue.auth.isAuthenticated()) {
        next({
          path: '/login',
        });
      } else if (Vue.auth.isAuthenticated() && Vue.auth.getUserRole() === 'ROLE_admin') {
        next({
          path: '/dashboardadmin',
        });
      } else if (Vue.auth.isAuthenticated() && Vue.auth.getUserRole() === 'ROLE_anggota') {
        next({
          path: '/dashboardanggotakoperasi',
        });
      } else if (Vue.auth.getUserRole() === 'ROLE_koperasi') {
        if (parseInt(Vue.auth.isHaveKoperasi()) === 0 && to.path !== '/daftarkoperasi') {
          next({
            path: '/daftarkoperasi',
          });
        } else if (parseInt(Vue.auth.isHaveKoperasi()) === 2 && to.path !== '/pendingactivation') {
          next({
            path: '/pendingactivation',
          });
        } else if (parseInt(Vue.auth.isHaveKoperasi()) === 3) {
          if (to.path !== '/dashboardkoperasi' && from.path !== '/dashboardkoperasi' && !to.matched.some(record => record.meta.roolTo)) {
            next({
              path: '/dashboardkoperasi',
            });
          } else {
            next();
          }
        } else {
          next();
        }
      } else {
        next();
      }
    } else if (to.matched.some(record => record.meta.forAdmin)) {
      if (!Vue.auth.isAuthenticated()) {
        next({
          path: '/login',
        });
      } else if (Vue.auth.isAuthenticated() && Vue.auth.getUserRole() === 'ROLE_anggota') {
        next({
          path: '/dashboardanggotakoperasi',
        });
      } else if (Vue.auth.isAuthenticated() && Vue.auth.getUserRole() === 'ROLE_koperasi') {
        if (parseInt(Vue.auth.isHaveKoperasi()) === 0) {
          next({
            path: '/daftarkoperasi',
          });
        } else if (parseInt(Vue.auth.isHaveKoperasi()) === 2) {
          next({
            path: '/pendingactivation',
          });
        } else if (parseInt(Vue.auth.isHaveKoperasi()) === 3) {
          next({
            path: '/dashboardkoperasi',
          });
        } else {
          next();
        }
      } else {
        next();
      }
    } else if (to.matched.some(record => record.meta.forAnggota)) {
      if (!Vue.auth.isAuthenticated()) {
        next({
          path: '/login',
        });
      } else if (Vue.auth.isAuthenticated() && Vue.auth.getUserRole() === 'ROLE_koperasi') {
        if (parseInt(Vue.auth.isHaveKoperasi()) === 0) {
          next({
            path: '/daftarkoperasi',
          });
        } else if (parseInt(Vue.auth.isHaveKoperasi()) === 2) {
          next({
            path: '/pendingactivation',
          });
        } else if (parseInt(Vue.auth.isHaveKoperasi()) === 3) {
          next({
            path: '/dashboardkoperasi',
          });
        } else if (Vue.auth.isAuthenticated() && Vue.auth.getUserRole() === 'ROLE_admin') {
          next({
            path: '/dashboardadmin',
          });
        } else {
          next();
        }
      } else {
        next();
      }
    } else {
      next();
    }
  },
);
export default router;
